body{
  width: 100vw;
  height: 100vh;
  background-color: #F8F8F8;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
}

.App{
  display: flex;
  flex-direction: row;
}

.monthly{
  width: 52%;
  height: 100vh;
  display: flex;
}

.daily{
  width:48%;
  height: 100vh;
}

.date-wrapper, .month, .year{
  font-weight: 600;
  color: #6E6B7B;
}

.date-wrapper{
  padding-top:3.5em;
}

.date{
  font-size: 4.5em;
  margin: 0;
  line-height: 80%;
}

.day{
  margin: 0;
  font-size: 2.5em;
}

.month{
  margin: 0;
  font-size: 2.8em;
}

.year{
  margin: 0;
  font-size: 1.4em;
}

.todolist-wrapper{
  /* background-color: aquamarine; */
  width: 65%;
  padding: 2.5em;
  margin-left: auto;
}

.todolist-header{
  font-size: 16px;
  font-weight: 600;
  color: #6E6B7B;
  margin: 0;
  margin-bottom: 0.5em;
}

.todolist-container{
  background-color: #ffffff;
  box-sizing: content-box;
  overflow: scroll;
  height: 100%;
  border-radius: 0.5em;
  border: 1px solid #EBE9F1;
  color: #6E6B7B;
}

.input-container{
  box-sizing: border-box;
  width: 100%;
  padding: 0.5em 1em;
  display: flex;
  border-bottom: 1px solid #EBE9F1;
}

.input-task{
  width: 70%;
  font-family: 'Montserrat', sans-serif;
  padding: 0.5em;
  border-style: none;
  outline: none;
  color: #6E6B7B;
}

::placeholder{
  color: #B9B9C3;
}

.input-task:focus::placeholder{
  padding-left: 0.4em;
  transition: padding-left 0.2s ease;
}

.add-task-btn{
  position: relative;
  overflow: hidden;
  display: block;
  margin-left: auto;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8em;
  font-weight: 500;
  border-style: none;
  border-radius: 2em;
  background-color: #EA5455;
  color: #ffffff;
  padding: 0 1.7em;
  transition: box-shadow .2s ease;
}

.add-task-btn:active, .add-task-btn:focus{
  box-shadow: none;
}

.ripple {
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  animation: ripple 600ms linear;
  background-color: rgba(255, 255, 255, 0.3);
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

.todolist{
  margin: 0;
  padding: 0;
}


.task{
  list-style: none;
  padding:0.7em 1em;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #EBE9F1;
}

.delete-task-btn{
  display: block;
  margin-left: auto;
  cursor: pointer;
  font-size: 18px;
  color: #D8D6DE;
}

.delete-task-btn:hover{
  color: #B9B9C3;
  transition: color .3s ease;
}

.checkbox-input{
  display: none;
}

.checkbox{
  width: 1rem;
  height: 1rem;
  border: 1px solid #D8D6DE;
  border-radius: 50%;
  margin-right: 0.5em;
  cursor: pointer;
}

.checkmark{
  visibility: hidden;
  color: #ffffff;
  font-size: 1rem;
}

.checked{
  background-color: #EA5455;
  border: 1px solid #EA5455;
  box-shadow: 0 2px 5px rgb(234, 84, 85, 0.5);
}

.checked + span{
  color: #B9B9C3;
}

.checked > .checkmark {
  visibility: visible;
}  

.note-section{
  height: 55%;
  padding: 2.5em;
  padding-left: 0;
  box-sizing: border-box;
}

.note-container{
  background-color: #ffffff;
  border-radius: 0.5em;
  height: 100%;
  box-shadow: 0 4px 24px 0 rgb(34, 41, 47, 0.1);
  display: flex;
  flex-direction: column;
  padding-bottom: 0.5em;
  box-sizing: border-box;
}

.note-header{
  display: flex;
}

.note-header-title{
  font-size: 1.5em;
  font-weight: 600;
  color: #6E6B7B;
  padding: 0.6em 0.8em;
  margin:0;
}

.edit-btn, .done-btn{
  color: #6E6B7B;
  font-size: 1.5em;
  margin-left:auto;
  padding: 0.6em 0.8em;
  cursor: pointer;
}

.note-title, .note{
  width: 90%;
  margin: auto;
  padding: 0.6em;
  font-size: 1em;
  font-family: 'Montserrat', sans-serif;
  border: 1px solid #EBE9F1;
  border-radius: 0.5em;
  color: #6E6B7B;
  outline: none;
}

.note-title:focus,
.note:focus{
  border:1px solid #EA5455;
  box-shadow: 0px 0px 18px 0px #EBEBEB;
}

.note-title:focus::placeholder{
  padding-left: 0.4em;
  transition: padding-left 0.2s ease;
}

.note{
  margin: 1em auto;
  height: 60%;
  resize: none;
} 

.title{
  font-weight: 500;
  font-size: 1.3em;
  margin: 0;
  padding: 0.3em 1.2em;
  color: #5E5873;
}

.content{
  overflow: scroll;
  color: #5E5873;
  padding: 1em 1.5em;
  margin:0;
}

.calendar-section{
  width: 60%;
  padding: 2.5em 3em;
}

.calendar-container{
  background-color: #ffffff;
  width:100%;
  height: 100%;
  border-radius: 0.5em;
  box-shadow: 0 4px 24px 0 rgb(34, 41, 47, 0.1);
  display: flex;
  flex-direction: column;
}

.calendar-header{
  padding: 1.7em 1.5em 1.2em 1.5em;
  display: flex;
}

.btn-group-wrapper{
  margin-left: auto;
}

.button-group{
  display: flex;
  margin-top: 0.5em;
}

.prev-btn, .today-btn, .next-btn{
  background-color: #ffffff;
  color: #6E6B7B;
  font-family: 'Montserrat';
  font-weight: 600;
  border: 0.7px solid #EBE9F1;
  cursor: pointer;
}

.prev-btn:hover,
.next-btn:hover,
.today-btn:hover{
  background-color: rgba(110, 107, 123, 0.03);
}

.prev-btn, .next-btn{
  font-size: 1.2em;
  border-radius: 0.3em;
  padding: 0;
}

.prev-btn{
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.next-btn{
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.today-btn{
  font-size: 0.6em;
  padding: 0.2em 1.6em;
  border-style: solid none solid none;
}

.arrow{
  display: block;
  padding: 0.1em;
}

.calendar{
  /* background-color: #7367f0; */
  width: 90%;
  margin: 0 auto;
  color: #6E6B7B;
  text-align: center;
  border-spacing: 0.4em;
  table-layout: fixed;
}

.row-day{
  color: #B9B9C3;
  font-size: 11px;
  font-weight: 500;
  padding-bottom: 0.85em;
}

.circle-wrapper{
  height: 35px;
}

.circle{
  width: 33px;
  height: 33px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  border-radius: 50%;
  cursor: pointer;
}

.today{
  color: #EA5455;
  border: 1px solid #EA5455;
}

.circle.selected{
  background-color: #EA5455;
  color: #ffffff;
  font-weight:500;
}

.prev-month, .next-month{
  color: #EEE9F1;
}

.category-section{
  /* background-color: chocolate; */
  width: 40%;
}

.category-wrapper{
  /* background-color: cornflowerblue; */
  margin-top: 15em;
  padding: 1.8em;
  padding-right: 0;
  font-weight: 600;
}

.category-header{
  color: #b9b9c3;
  display: flex;
  font-size: 0.9em;
  margin-bottom: 1.5em;
  margin-left: 1em;
}

.show-inputgroup-btn{
  font-size: 1.3em;
  display: block;
  margin-left: auto;
  cursor: pointer;
}

.category-list{
  margin: 0;
  padding: 0;
  list-style: none;
  color: #4e5154;
}

.category-label{
  padding: 0.45em;
  margin: 0;
  display: flex;
}

.category-label:hover{
  background-color: #ffffff;
  border-radius: 0.4em;
}

.category-label:hover > .delete-category-btn{
  color: #B9B9C3;
}

.delete-category-btn{
  font-size: 1.3em;
  display: block;
  margin-left: auto;
  color:#F8F8F8;
}

.delete-category-btn:hover{
  cursor: pointer;
}

.bullet-styling{
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  display: inline-block;
}

.bullet{
  width: 0.75rem;
  height: 0.75rem;
  margin:auto 1em auto 0.4em;
}

.bullet-default{
  background-color: #7367f0;
}

.category-input-group{
  border-radius: 0.4em;
  display: flex;
}

.bullet-wrapper{
  background-color: #ffffff;
  border: 1px solid #EBE9F1;
  border-style: solid none solid solid;
  border-radius: 0.4em;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  display: flex;
  padding-left: 0.4em;
}

.category-input-group:active ,
.category-input-group:focus-within{
  box-shadow: 0px 0px 10px 0px  rgba(146, 146, 146, 0.1);
}

.category-input{
  padding: 0.55em;
  border: 1px solid #EBE9F1;
  border-style: solid none solid solid;
  width: 70%;
  font-family: 'Montserrat', sans-serif;
  outline: none;
}

.category-input:focus::placeholder{
  padding-left: 0.4em;
  transition: padding-left 0.2s ease;
}

.add-calendar-btn{
  background-color: #ffffff;
  border:  1px solid #EBE9F1;
  border-radius: 0.4em;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  width: 20%;
  margin-left: auto;
  display: flex;
}

.add-calendar-btn:hover{
  border:  1px solid #EA5455;
}

.add-btn-mark{
  font-size: 1.3em;
  cursor: pointer;
  color: #EBE9F1;
  margin: auto;
  display: inline-block;
}

.add-calendar-btn:hover > .add-btn-mark{
  color: #EA5455;
}

.add-event-btn{
  background-color: #ffffff;
  padding: 0.7em 2.5em;
  border: 1px solid #6E6B7B;
  border-radius: 0.5em;
  font-size: 0.8em;
  font-weight: 600;
  color: #6E6B7B;
  font-family: 'Montserrat';
  display: block;
  margin: 2.3em  auto;
  cursor: pointer;
}

.add-event-btn:hover{
  background-color: rgba(110, 107, 123, 0.06);
}

.timeline{
  /* background-color: aquamarine; */
  list-style-type: none;
  padding: 1em 2.5em;
  padding-right: 2em;
  margin:0;
  overflow:scroll;
}

.timeline-item{
  margin: 0;
  border: 1px solid #EBE9F1;
  border-style: none none none solid;
  position: relative;
  padding-left: 2em;
}

.timeline-item:hover{
  background-color: rgba(233, 233, 233, 0.123);
  cursor: pointer;
}

.timeline-point{
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin: 4px auto;
}

.timeline-point-wrapper{
  width: 20px;
  height: 20px;
  position: absolute;
  border-radius: 50%;
  top:-10px;
  left: -10.5px;
}

.place-icon{
  color: #EEE9F1;
  font-size: 1.1em;
  padding: 0.2em;
}

.event-wrapper{
  display: flex;
  align-items: baseline;
}

.event-title{
  font-weight: 600;
  color: #6E6B7B; 
}

.event-time{
  display: block;
  margin-left: auto;
  font-size: 0.8em;
  color: #b9b9c3;
}

.event-place{
  padding-bottom: 3em;
  padding-top: 0.4em;
  color: #6E6B7B;
  font-size: 0.8em;
  margin:0;
}

.modal-background{
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(110, 107, 123, 0.45);
  z-index: 3;
  width: 100vw;
  height: 100vh;
}

.modal{
  display: flex;
  flex-direction: column;
  z-index: 10;
  background-color: #ffffff;
  width: 340px;
  /* height: 400px; */
  margin: auto;
  border-radius: 0.5em;
  box-shadow: 3px 3px 30px rgba(110, 107, 123, 0.25);
}

.modal-header{
  background-color: #F8F8F8;
  border-radius: 0.5em;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  color: #6E6B7B;
  font-weight: 600;
  font-size: 1.1em;
  padding: 0.7em 1em;
  display: flex;
}

.modal form{
  padding: 0.3em 0 0.8em 0;
}

.delete-event-btn{
  font-size: 1.3em;
  margin-left: auto;
  cursor: pointer;
}

.event-input-group{
  /* background-color: yellow; */
  padding: 0.5em 1.2em;
}

.event-input-group label{
  display: inline-block;
  color:#6E6B7B;
  font-size: 0.9em;
  padding-bottom: 0.3em;
}

.event-inputbox-wrapper, .timepicker-wrapper{
  background-color: #ffffff;
  padding: 0.5em;
  border-radius: 0.25em;
  border: 1px solid #D8D6DE;
}

.event-input-group input[type=text]{
  outline: none;
  width: 95%;
  border: none;
  font-family: 'Montserrat', sans-serif;
  color:#6E6B7B;
}

.modal-footer{
  border: 1px solid #F8F8F8;
  border-style: solid none none none;
  padding: 1.2em;
  display: flex;
  justify-content: flex-end;
}

.close-modal{
  font-family: 'Montserrat';
  font-weight: 600;
  color: #6E6B7B;
  border: 1px solid #6E6B7B;
  border-radius: 0.3em;
  background-color: #ffffff;
  padding: 0.5em 1em;
  cursor: pointer;
}

.close-modal:hover{
  background-color: rgba(110, 107, 123, 0.06);
}

.save-event-btn{
  font-family: 'Montserrat';
  font-weight: 600;
  color: #ffffff;
  border: 1px solid #EA5455;
  border-radius: 0.3em;
  background-color: #EA5455;
  padding: 0.5em 1.5em;
  margin-left: 0.5em;
  cursor: pointer;
}

.save-event-btn:hover{
  background-color: #e63f3f;
}

.select-category{
  display: flex;
  justify-content: center;
}

.event-category{
  appearance: none;
  background-color: red;
  width: 13px; 
  height: 13px;
  border-radius: 50%;
  margin: 0.2em 1.2em;
  cursor: pointer;
  position: relative;
}

.event-category:hover::before,
.event-category:checked::before{
  content: "";
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #D8D6DE;
  position: absolute;
  top: -4.5px;
  left: -4.5px;
}

.timepicker-group{
  display: flex;
  align-items: center;
  width: 100%;
}

.timepicker-group span{
  margin: 1em;
  color:#6E6B7B;
}

.timepicker-wrapper{
  display: flex;
  justify-content: center;
  align-content: center;
}

.timepicker-wrapper div{
  /* display: flex; */
  background-color: aquamarine;
  position: relative;
  width: 48px;
}

.timepicker{
  z-index: 50;
  width: inherit;
  appearance: none;
  outline: none;
  border: none;
  font-family: 'Montserrat', sans-serif;
  color:#6E6B7B; 
  cursor: pointer;
  padding: 0.1em 0.3em 0.1em 0.5em;
  letter-spacing: 0.1em;
  /* background-color: #7367f0; */
}

.time-select-btn{
  z-index: 15;
  color: #D8D6DE;
  font-size: 1.5em;
  cursor: pointer;
  position:absolute;
  top: 0;
  right: 0;
}

.timepicker-wrapper span{
  margin: 0 0.7em;
  font-weight: 600;
}

